

const Counter = ({setNewIndex}) => {
  return (
    <div className="counter">
      <button onClick={()=>setNewIndex(0) } className='counterBtn'>01</button>
      <button onClick={()=>setNewIndex(1) } className='counterBtn'>02</button>  
      <button onClick={()=>setNewIndex(2) } className='counterBtn'>03</button>
      <button onClick={()=>setNewIndex(3) } className='counterBtn'>04</button>
      <button onClick={()=>setNewIndex(4) } className='counterBtn'>05</button>
      <button onClick={()=>setNewIndex(5) } className='counterBtn'>06</button>  
  </div>
  )
}

export default Counter;