// import { ClassNames } from '@emotion/react';
import './assets/Variables/root.scss';
import Portofolio from './Components/Portofolio';
import RegumTeam from './Components/RegumTeam/RegumTeam';
import ContactUs from './Components/ContactUs/ContactUs';
import Footer from './Components/Footer/Footer' ;
import Hero from './Components/Hero';
import  Navbar  from './Components/Navbar';
import AboutUs from './Components/AboutUs/AboutUs'
import Testimonials from './Components/Testimonials';
import WhatWeDo from './Components/WhatWeDo';
import WhyUs from './Components/whyUs';
import OurBlogs from './Components/OurBlogs';
import ScrollDotts from './Components/ScrollDotts';

function App() {
  const gra = function(min, max) {
    return Math.random() * (max - min) + min;
  };
  const init = function() {
    let items = document.querySelectorAll('.sec');

    for (let i = 0; i < items.length; i++) {
      items[i].style.minHeight = gra(120, 205) + "vh";
      items[i].style.background = randomColor({ luminosity: "light" });
    }
  
    cssScrollSnapPolyfill();
  };
  init();


  return (
  <>

    
    <div className='scroll'>
    <ScrollDotts/>
    <Navbar/> 
    <Hero/>
    <AboutUs/>
    <WhatWeDo />
    <WhyUs />
    <Portofolio/>
    <Testimonials/>
    <OurBlogs />
    {/* <RegumTeam/>  */}
    <ContactUs/>
    <Footer/>
    </div>




  
 
</>

  );
}

export default App;
