import React, { useState } from "react";
import "./popup.scss";
import Exit from "../../assets/images/Logo/Exit.png";


export default function Modal({blogData}) {
    const [popup, setPopup] = useState(false);

    const togglePopup = () => {
        setPopup(!popup);
    };

    if(popup) {
        document.body.classList.add('active-popUp' )
    } else {
        document.body.classList.remove('active-popUp' )
    }

    return (
        <>
        <button onClick={togglePopup} className="btn-popUp">
            view blog
        </button>




        {popup && (
            <div className="popUp">
                <div className="background"></div>
            <div onClick={togglePopup} className="overlay"></div>
                <div className="popUp-content">


                    <div className="group-data">

                        <div className="title-date">
                            <p className="p-Date">{blogData.Date}</p>
                            <h2 className="title-popUp">{blogData.Title}</h2>
                        </div>


                    <div className="containdiv">
                        <div className="imgdiv">
                            <img 
                                src={blogData.Card} 
                                alt="blog"    
                            />
                        </div>
                    </div>
                    <div className="d-dec-popUp">
                        <p className="dec-popUp">
                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Provident
                            perferendis suscipit officia recusandae, eveniet quaerat assumenda
                            id fugit, dignissimos maxime non natus placeat illo iusto!
                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Provident
                            perferendis suscipit officia recusandae, eveniet quaerat assumenda
                            id fugit, dignissimos maxime non natus placeat illo iusto!
                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Provident
                            perferendis suscipit officia recusandae, eveniet quaerat assumenda
                            id fugit, dignissimos maxime non natus placeat illo iusto!
                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Provident
                            perferendis suscipit officia recusandae, eveniet quaerat assumenda
                            id fugit, dignissimos maxime non natus placeat illo iusto!
                        </p>
                    </div>
                </div>
                    <button className="close-popUp" onClick={togglePopup}>
                        <img 
                            src={Exit}
                            alt="Exit"
                        />
                    </button>
                </div>
            </div>
        )}
        </>
    );
}