import React, { useState, useEffect } from 'react';
import "./RegumTeam.scss";
import Slider from "../slider/Slider";
import { useSelector } from "react-redux";
import ReactLoading from "react-loading";



function RegumTeam() {


  const dataApiPromise = useSelector((state) => state.apiReducer);


  const [dataApi, setDataApi] = useState([]);


  useEffect(() => {

    dataApiPromise.then(function (result) {
      setDataApi(result);
    });
  }, [dataApiPromise]);



  //  ********* Loading *************
  let color = "#F5024D"
  let type = "bubbles"


  while (!dataApi[0]) {

    return (

      <div className="portofolio regumTeamContainer sec" id="Team">
        <div className="regumTeamTitle componentTitle">
          <h1>Regum <span>Team</span></h1>
          <hr></hr>
        </div>
        <ReactLoading className='regumTeamLoadingIcon' type={type} color={color} height={100} width={100} />
      </div>
    );
  }






  return (
    <>


      <div className="portofolio regumTeamContainer" id="Team">
        <div className="regumTeamTitle componentTitle">
          <h1>Regum <span>Team</span></h1>
          <hr></hr>
        </div>

        <div className="secondSection">
          <div className="regumTeamPhotos">
            <Slider />
          </div>
          <div className="regumteamdataContainer">
            <div className="regumTeamDataHolder">
              <div className="regumTeamDatasecondHolder">
                <div className="regumTeamData">
                  <p className="regumTeamName">{dataApi[0].name}</p>
                  <p className="regumTeamPosition">{dataApi[0].position}</p>
                </div>
              </div>
            </div>

          </div>

        </div>


      </div>


    </>
  );
}


export default RegumTeam;