import React from 'react';
import './index.scss';
import { Fade } from 'react-reveal';
import Logo from '../../assets/images/Logo/WhyUs/WhyUs.png';

const index = () => {

const Title = "sample headline";
const Description = "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud ";

return (
        <>
            <div className="WhyUs sec" id='WhyUs'>
                <div className="componentTitle">
                        <Fade right duration={800}  delay={1100}> 
                        <h1><span>Why</span>Us</h1> </Fade>
                        <Fade right duration={800}  delay={400}>
                        <hr />
                        </Fade>
                    </div>
                <div className="container">
                    <main className='row'>
                        {/* =========================[timeLine---------->1]======================= */}
                        
                        <section className="col-1">
                            <div className="contents">
                                <Fade bottom duration={800} >
                                    <div className='box'>
                                    <div className="box-img">
                                        <img src={Logo} alt="Logo"/>
                                    </div>
                                    <div className="box-TD">
                                        <h1>{Title}</h1>
                                        <p>{Description}</p>    
                                    </div>
                                </div>
                                </Fade> 
                                <Fade bottom duration={800} delay={800}>
                                    <div className='box'>
                                        <div className="box-img">
                                            <img src={Logo} alt="Logo"/>
                                        </div>
                                        <div className="box-TD">
                                            <h1>{Title}</h1>
                                            <p>{Description}</p>    
                                        </div>
                                    </div>
                                </Fade>
                                <Fade bottom duration={800} delay={1600}>
                                    <div className='box'>
                                        <div className="box-img">
                                            <img src={Logo} alt="Logo"/>
                                        </div>
                                        <div className="box-TD">
                                            <h1>{Title}</h1>
                                            <p>{Description}</p>    
                                        </div>
                                    </div>
                                </Fade>
                            </div>
                        </section>
                        {/* =========================[timeLine---------->2]======================= */}
                        <section className="col-2">
                            <div className="contents">
                                <Fade bottom duration={800} delay={2400}>
                                    <div className='box'>
                                        <div className="box-img">
                                            <img src={Logo} alt="Logo"/>
                                        </div>
                                        <div className="box-TD">
                                            <h1>{Title}</h1>
                                            <p>{Description}</p>    
                                        </div>
                                    </div>
                                </Fade>
                                <Fade bottom duration={800} delay={0}>
                                    <div className='box'>
                                        <div className="box-img">
                                            <img src={Logo} alt="Logo"/>
                                        </div>
                                        <div className="box-TD">
                                            <h1>{Title}</h1>
                                            <p>{Description}</p>    
                                        </div>
                                    </div>
                                </Fade>
                                <Fade bottom duration={800} delay={800}>
                                    <div className='box'>
                                        <div className="box-img">
                                            <img src={Logo} alt="Logo"/>
                                        </div>
                                        <div className="box-TD">
                                            <h1>{Title}</h1>
                                            <p>{Description}</p>    
                                        </div>
                                    </div>
                                </Fade>
                            </div>
                        </section>
                        
                    </main>
                </div>
                <hr className="buttomLine"/>
            </div>
        </>
    )
}

export default index;