import "./index.scss"
import Logo from "../../assets/images/Regum logo/Regumapp-1.png"
import { useState ,useEffect ,useRef, createRef } from "react"
import Hamburger from "./hamburger";
import { create } from "@mui/material/styles/createTransitions";


const Navbar = () => {

const [list,setList]=useState(true);
const [sideList,setSideList]=useState(false);

let closeNav =createRef();
const closeHamb = () => {
    closeNav.current.click();
}

function NavLinks(){
    if(list){
        return(
            <>
              <a href='#WhyUs' className="grid">Why us</a>
              <a href='#Services' className="grid">Services</a>
              <a href='#Portofolio' className="grid">Portofolio</a>
              <a href='#Testimonials' className="grid">Testimonials</a>
              <a href='#Blogs' className="grid">Blogs</a>
              <a href='#Team' className="grid">Team</a> 
            </>
        )
    }else if(sideList){
        return(    
            <ul className="side-list">
            <li><a href='#AboutUs' className="grid"      onClick={closeHamb}>About us</a></li>
            <li><a href='#Contact' className="grid"      onClick={closeHamb}>Contact us</a></li>
            <li><a href='#WhyUs' className="grid"        onClick={closeHamb}>Why us</a></li>
            <li><a href='#Services' className="grid"     onClick={closeHamb}>Services</a></li>
            <li><a href='#Portofolio' className="grid"   onClick={closeHamb}>Portofolio</a></li>
            <li><a href='#Testimonials' className="grid" onClick={closeHamb}>Testimonials</a></li>
            <li><a href='#Blogs' className="grid"        onClick={closeHamb}>Blogs</a></li>
            <li><a href='#Team' className="grid"         onClick={closeHamb}>Team</a> </li> 
            </ul>
        )
   
    }

}
useEffect(() => {
    const windowWidth=  document.documentElement.clientWidth;
    if(windowWidth <= 980){
        setList(false)
        setSideList(true)
    }else {
        setList(true)
        setSideList(false)
    }
},[]);



    return ( <>
    <nav id="Nav" className="sec"> 
        
        <section className="upperNav"> 
            <div className="dataContact">
                <ul>
                    <li>regumapps.gmail.com</li>
                    <li>0152-1556-688</li>
                </ul>
            </div>
            <div className="rigtUpper snav">
                <ul className="right">
                    {!sideList && <> <li><a href='#AboutUs' className="grid">About us</a></li>
                    <li><a href='#Contact' className="grid">Contact us</a></li> </>}
                </ul>
                {sideList && <NavLinks/>}
            </div>
            <Hamburger closeNav={closeNav} />
        </section> 
        <section className="downNav ">
            
            <div className="grid"><img className="logo" src={Logo} alt="logo" /></div>
            {list &&  <NavLinks/> }
        </section>
    </nav>  
    </>);
}
 
export default Navbar;