import Popup from "./Popup"


const SectionBlog = ({blogData}) => {
return (
    <div className="sectionBlog">
        <div className="dDate">
            <p className="date">{blogData.Date}</p>
        </div>
        <div className="dTitle ">
            <h1 className="title">{blogData.Title}</h1>
            <div className="dVew  ">
                <Popup blogData={blogData}/>
            </div>
        </div>
            <div className="dCard">
                <img 
                    src={blogData.Card} 
                    alt="blog"
                    className="imgCard"
                />
                </div>
    </div>
)
}

export default SectionBlog