import "./index.scss"
import { useState ,useEffect} from "react";

const ScrollDotts = () => {

    const[dots,setDots]=useState(true);
    useEffect(() => {
        const windowWidth=  document.documentElement.clientWidth;
        if(windowWidth <= 980) setDots(false)
        else setDots(true)

    },[]);

    console.log(dots);

    function Scrolldott(){
        return(
        <div className="scrolldotts">
            <ul>
                <li className="dot" data-scroll="Hero"><a href="#Hero"                ><span>Hero</span></a></li>
                <li className="dot" data-scroll="AboutUs"><a href="#AboutUs"          ><span>About</span></a></li>
                <li className="dot" data-scroll="Services"><a href="#Services"        ><span>Services</span></a></li>
                <li className="dot" data-scroll="WhyUs"><a href="#WhyUs"              ><span>Why Us</span></a></li>
                <li className="dot" data-scroll="Portofolio"><a href="#Portofolio"    ><span>Portofolio</span></a></li>
                <li className="dot" data-scroll="Testimonials"><a href="#Testimonials"><span>Testimonials</span></a></li>
                <li className="dot" data-scroll="Blogs"><a href="#Blogs"              ><span>OurBlogs</span></a></li>
                <li className="dot" data-scroll="Team"><a href="#Team"                ><span>Regum Team</span></a></li>
                <li className="dot" data-scroll="Contact"><a href="#Contact"          ><span>Contact Us</span></a></li>
                
            </ul>
         </div>
        )
    
    }

$(document).ready(function(){{
$('.scroll').on('scroll',function(){
    var link = $('.scrolldotts .dot');
    var top  = $(window).scrollTop();
    $('.sec').each(function(){
        var id = $(this).attr('id');
        var height = $(this).height();
        var offset = $(this).offset().top - 150; 
        if(top >= offset && top < offset +height){
            link.removeClass('active');
            $('.scrolldotts ul  ').find('[data-scroll="' + id + '"]').addClass('active');
        }
    })
})
}});

    return (<>

{dots && <Scrolldott/>}


    </>);
}

export default ScrollDotts;